import myAxios from "@/request";

/**
 * 
 * @param params
 */
export const userRegister = async (params: any) => {
  return myAxios.request({
    url: "api/user/register",
    method: "POST",
    data: params,
  });
};

/**
 * 
 * @param params
 */
export const userLogin = async (params: any) => {
  return myAxios.request({
    url: "api/user/login",
    method: "POST",
    data: params,
  });
};

/**
 * 
 * @param params
 */
export const userLogout = async (params: any) => {
  return myAxios.request({
    url: "api/user/logout",
    method: "POST",
    data: params,
  });
};

/**
 * 
 */
export const getCurrentUser = async () => {
  return myAxios.request({
    url: "api/user/current",
    method: "GET",
  });
};

/**
 * 
 * @param userName
 */
export const searchUsers = async (userName: any) => {
  return myAxios.request({
    url: "api/user/search",
    method: "GET",
    params: {
      userName,
    },
  });
};

/**
 * 
 * @param id
 */
export const deleteUser = async (id: string) => {
  return myAxios.request({
    url: "api/user/delete",
    method: "POST",
    data: id,
    headers: {
      "Content-Type": "application/json",
    },
  });
};
// 
export const getReportByPage = async (params: {
  pageIndex: number;
  pageSize: number;
  Type: number;
}) => {
  return myAxios.request({
    url: "/api/ReportRecord/GetReportByPage", // 
    method: "GET",
    params: params,
  });
};

//  
export const downloadReport = async (timeStr: string, tabKey: number) => {
  return myAxios.request({
    url: "api/Report/DownloadExcel",
    method: "GET",
    params: { timeStr: timeStr, Type: tabKey },
    responseType: "blob",
    timeout: 60000,
  });
};
// ZIP
//  api/user.ts
export const batchDownloadReportZip = async (params: {
  type: number; //  1- 2- 3- 4-
  timeStr: string; // YYYY/YYYY-MM/YYYY-MM-DD
}) => {
  return myAxios.request({
    url: "/api/File/ZipDownloadFile", // 
    method: "GET", //  GET 
    params: params, // GET  params  URL
    responseType: "blob", //  blob 
    timeout: 120000, // 
  });
};
