import axios, { AxiosResponse, AxiosError } from "axios";
import { message } from "ant-design-vue";
import router from "@/router"; // 
import { useLoginUserStore } from "@/store/useLoginUserStore"; // Pinia store
import {
  checkSessionTimeout,
  handleSessionTimeout,
} from "@/utils/sessionTimeout";

// AxiosResponse
declare module "axios" {
  interface AxiosResponse {
    fileBlobData?: Blob;
    fileDownloadName?: string;
  }
}

// 401
let isRedirecting = false;

// axios

const myAxios = axios.create({
  // IP
  baseURL:
    process.env.NODE_ENV === "development"
      ? "http://localhost:5260"
      : window.location.origin, // /IP
  timeout: 10000,
  withCredentials: true, // cookiesession
});
// 
myAxios.interceptors.request.use(
  function (config) {
    if (!config.headers["Content-Type"]) {
      config.headers["Content-Type"] = "application/json;charset=UTF-8";
    }
    const loginUserStore = useLoginUserStore();
    // loginUsertokenlocalStorage
    if (loginUserStore.loginUser.token) {
      config.headers.Authorization = `Bearer ${loginUserStore.loginUser.token}`;
    }
    return config;
  },
  function (error: AxiosError) {
    message.error("");
    console.error("", error);
    return Promise.reject(error);
  }
);

// 
myAxios.interceptors.response.use(
  function (response: AxiosResponse): AxiosResponse {
    //   
    const loginUserStore = useLoginUserStore();
    if (loginUserStore.loginUser.id && checkSessionTimeout()) {
      handleSessionTimeout();
      return response;
    }

    // blob
    if (response.config.responseType === "blob") {
      let fileName = ".xlsx";
      if (
        response.headers["content-disposition"] ||
        response.headers["Content-Disposition"]
      ) {
        try {
          const disposition =
            response.headers["content-disposition"] ||
            response.headers["Content-Disposition"];
          fileName = decodeURI(disposition.split("filename=")[1]);
          fileName = fileName.replace(/"/g, "");
        } catch (err) {
          fileName = ".xlsx";
        }
      }
      response.fileBlobData = response.data as Blob;
      response.fileDownloadName = fileName;
      return response;
    }

    // JSON
    const { data } = response;
    console.log("", data);

    // 40100.value
    if (data.code === 40100) {
      const isUserCurrentApi =
        response.request.responseURL.includes("user/current");
      const isLoginPage =
        router.currentRoute.value.path.includes("/user/login");

      if (!isUserCurrentApi && !isLoginPage && !isRedirecting) {
        isRedirecting = true;
        message.warning("");

        const loginUserStore = useLoginUserStore();
        loginUserStore.clearLoginUser(); // clearLoginUsersessionStorage

        router
          .push({
            path: "/user/login",
            query: {
              redirect: encodeURIComponent(router.currentRoute.value.fullPath),
            },
          })
          .finally(() => {
            isRedirecting = false;
          });
      }
    }
    // 
    if (data.code && data.code !== 0 && data.code !== 40100) {
      message.error(data.message || `${data.code}`);
    }

    return response;
  },
  function (error: AxiosError) {
    if (error.response) {
      const status = error.response.status;
      // 401 HTTP.value
      if (status === 401 && !isRedirecting) {
        // 3router.currentRoute.path  router.currentRoute.value.path
        const isLoginPage =
          router.currentRoute.value.path.includes("/user/login");
        if (!isLoginPage) {
          isRedirecting = true;
          message.warning("");

          const loginUserStore = useLoginUserStore();
          loginUserStore.setLoginUser({ id: "" });
          localStorage.removeItem("loginUser");

          // 4router.currentRoute.fullPath  router.currentRoute.value.fullPath
          router
            .push({
              path: "/user/login",
              query: {
                redirect: encodeURIComponent(
                  router.currentRoute.value.fullPath
                ),
              },
            })
            .finally(() => {
              isRedirecting = false;
            });
        }
      } else if (status !== 401) {
        message.error(`HTTP${status}`);
      }
    } else if (error.request) {
      message.error("");
    } else {
      message.error("" + error.message);
    }
    console.error("", error);
    return Promise.reject(error);
  }
);

export default myAxios;
