import { defineStore } from "pinia";
import { ref } from "vue";
import { getCurrentUser } from "@/api/user";

export const useLoginUserStore = defineStore("loginUser", () => {
  // sessionStorage
  const initLoginUser = () => {
    const savedUser = sessionStorage.getItem("loginUser");
    if (savedUser) {
      try {
        return JSON.parse(savedUser);
      } catch (e) {
        console.error("sessionStorage", e);
        return { userName: "" };
      }
    }
    return { userName: "" };
  };
  const loginUser = ref<any>(initLoginUser());

  // sessionStorage
  async function fetchLoginUser() {
    try {
      const res = await getCurrentUser();
      if (res.data.code === 0 && res.data.data) {
        console.log("", res.data.data);
        loginUser.value = res.data.data;
        sessionStorage.setItem("loginUser", JSON.stringify(res.data.data));
      }
    } catch (error) {
      console.error("", error);
    }
  }

  // sessionStorage
  function setLoginUser(newLoginUser: any) {
    loginUser.value = newLoginUser;
    if (newLoginUser.id) {
      sessionStorage.setItem("loginUser", JSON.stringify(newLoginUser));
    } else {
      sessionStorage.removeItem("loginUser");
    }
  }

  // sessionStorage
  function clearLoginUser() {
    loginUser.value = { userName: "" };
    sessionStorage.removeItem("loginUser");
  }

  // sessionStorage
  function restoreLoginUser() {
    const savedUser = sessionStorage.getItem("loginUser");
    if (savedUser) {
      try {
        loginUser.value = JSON.parse(savedUser);
      } catch (e) {
        console.error("", e);
        clearLoginUser();
      }
    }
  }

  return {
    loginUser,
    setLoginUser,
    fetchLoginUser,
    clearLoginUser,
    restoreLoginUser,
  };
});
