<template>
  <div class="layout-container">
    <!--  -->
    <aside class="sidebar">
      <!--  -->
      <div class="sidebar-header">
        <img class="logo" src="../assets/hb.png" alt="logo" />
      </div>

      <!--  -->
      <a-menu
        v-model:selectedKeys="current"
        mode="vertical"
        :items="items"
        @click="doMenuClick"
        class="sidebar-menu"
      />

      <!--  -->
      <div class="logout-btn">
        <a-button type="text" @click="handleLogout"></a-button>
      </div>
    </aside>

    <!--  -->
    <main class="main-content">
      <router-view />
    </main>
  </div>
</template>

<script lang="ts" setup>
import { ref, h } from "vue";
import { MenuProps } from "ant-design-vue";
import { useRouter } from "vue-router";
import { useLoginUserStore } from "@/store/useLoginUserStore";
import { message } from "ant-design-vue";
// 1.  EditOutlined 
import {
  HomeOutlined,
  DashboardOutlined,
  FileTextOutlined,
  EditOutlined,
} from "@ant-design/icons-vue";
import { userLogout } from "@/api/user";

const router = useRouter();
const loginUserStore = useLoginUserStore();

//  TS  h  VNode
const items = ref<MenuProps["items"]>([
  {
    key: "/app/components/leader-dashboard",
    label: "",
    title: "",
    icon: () => h(DashboardOutlined),
  },
  {
    key: "/app/home",
    label: "",
    title: "",
    // FileTextOutlined
    icon: () => h(FileTextOutlined),
  },
  {
    key: "/app/components/TableEditable",
    label: "",
    title: "",
    // 2.  DashboardOutlined  EditOutlined
    icon: () => h(EditOutlined),
  },
]);

// 
const doMenuClick = ({ key }: { key: string }) => {
  router.push(key);
};

// 
const current = ref<string[]>(["/app/home"]);
router.afterEach((to) => {
  current.value = [to.path];
});

// 
const handleLogout = async () => {
  try {
    await userLogout({});
    if (typeof loginUserStore.clearLoginUser === "function") {
      loginUserStore.clearLoginUser();
    } else {
      loginUserStore.loginUser = { userName: "" };
    }
    localStorage.removeItem("loginUser");
    localStorage.removeItem("token");
    clearAllCookies();
    message.success("");
    router.replace({ path: "/user/login" });
  } catch (error) {
    console.error("", error);
    if (typeof loginUserStore.clearLoginUser === "function") {
      loginUserStore.clearLoginUser();
    } else {
      loginUserStore.loginUser = { userName: "" };
    }
    localStorage.removeItem("loginUser");
    localStorage.removeItem("token");
    clearAllCookies();
    message.success("");
    router.replace({ path: "/user/login" });
  }
};

// Cookie
const clearAllCookies = () => {
  const cookies = document.cookie.split(";");
  for (let i = 0; i < cookies.length; i++) {
    const cookie = cookies[i];
    const eqPos = cookie.indexOf("=");
    const name = eqPos > -1 ? cookie.substr(0, eqPos).trim() : cookie.trim();
    document.cookie = `${name}=; expires=Thu, 01 Jan 1970 00:00:00 GMT; path=/; domain=${window.location.hostname}`;
  }
};
</script>

<style scoped>
/*  */
.layout-container {
  display: flex;

  height: 100vh;
  overflow: hidden;
}
.sidebar {
  width: 180px;
  height: 100%;
  background-color: #f5f9ff;
  color: #1e40af;
  box-shadow: 2px 0 12px rgba(0, 0, 0, 0.05);

  overflow-y: auto; /*  */
  transition: width 0.3s; /* / */
}
.sidebar-header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 12px;
  height: 60px;
  border-bottom: 1px solid #e0e7ff;
}
.logo {
  height: 30px;
  margin-right: 8px;
}
.title {
  color: #1e40af;
  font-size: 16px;
  font-weight: 600;
}
.sidebar-menu {
  border-right: none !important;
  padding: 12px 0;
  flex: 1;
}
.logout-btn {
  padding: 12px;
  border-top: 1px solid #e0e7ff;
}
.main-content {
  flex: 1;
  height: 100%;
  padding: 8px;
  background-color: #ffffff;
  overflow-y: auto;
}
:deep(.ant-menu-vertical .ant-menu-item) {
  height: 44px;
  line-height: 44px;
  margin: 0 8px !important;
  border-radius: 6px;
  padding-left: 20px !important;
  color: #1e40af !important;
  background-color: transparent !important;
}
:deep(.ant-menu-item-selected) {
  background-color: #3b82f6 !important;
  color: #ffffff !important;
}
:deep(.ant-menu-item:hover) {
  background-color: #dbeafe !important;
  color: #1e40af !important;
}
:deep(.ant-menu-title-content) {
  font-size: 14px;
  font-weight: 500;
}
:deep(.ant-btn-text) {
  color: #1e40af !important;
  display: block;
  width: 100%;
  text-align: center;
}
:deep(.ant-btn-text:hover) {
  background-color: #dbeafe !important;
  color: #1e40af !important;
}
</style>
